code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.squares {
    min-width: 70px;
    min-height: 70px;
    margin-bottom: 10px;
    margin-right: -80px;
    display: inline-block;
    border: 1px solid black;
    cursor: pointer;
    place-items: center;
    font-size: 30px;
    color: black;
}

.squares:active {
    background-color: darkblue;
}

.MuiGrid-root.MuiGrid-container.MuiGrid-item.MuiGrid-grid-xs-4 {
    margin-right: -70px;
}

.board {
    width: 500px;
    height: 500px;
    display: flex;
    justify-content: center;
    flex-direction: column;

}

.MuiFormControl-root {
    max-width: 80px;
}

.App {
    padding-left: 22px;
}

.buttonW {
    width: 150px;
    height: 150px;
}

.grid {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 1em 1em;
    grid-auto-flow: row dense;
}

.toast {
    margin-left: -100px;
}